/** Ultra Light */
@font-face {
  font-family: "San Francisco";
  font-weight: 100;
  src: url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-ultralight-webfont.woff");
}
/** Thin */
@font-face {
  font-family: "San Francisco";
  font-weight: 200;
  src: url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-thin-webfont.woff");
}
/** Regular */
@font-face {
  font-family: "San Francisco";
  font-weight: 400;
  src: url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-regular-webfont.woff");
}
[ng:cloak],
[ng-cloak],
[data-ng-cloak],
[x-ng-cloak],
.ng-cloak,
.x-ng-cloak {
  display: none !important;
}
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  margin-top: 300px;
}
.large-text {
  font-weight: 400;
  font-size: 60px;
}
.small-text {
  font-weight: 200;
  font-size: 18px;
}
.medium-text {
  font-weight: 400;
  font-size: 36px;
}
.light-text {
  font-weight: 100;
  font-size: 36px;
}
.main-container {
  background: #dd2e44;
  height: 200vh;
}
.main-nav-bar {
  background: linear-gradient(#dd2e44, transparent);
  color: #f2f2f2;
  height: 40vh;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 20;
}
.top-nav-tabs {
  font-family: 'San Francisco';
  font-weight: 100;
  font-size: 24;
  display: flex;
  justify-content: space-between;
/*home-view*/
}
.home-container {
  height: 300px;
  width: 100%;
  background: #dd2e44;
}
.nav-filter {
  text-align: center;
}
.nav-bottom {
  vertical-align: text-bottom;
}
.location-filter {
  background: transparent;
  border: none;
  font-color: #f2f2f2;
  font-size: 40px;
  font-weight: 400;
  text-align: center;
  padding-right: 0px;
  margin: 0px;
  width: 300px;
}
.location-filter::placeholder {
  color: #f2f2f2;
}
.location-filter:focus {
  outline: none;
}
li {
  display: inline-block;
}
a {
  text-decoration: none;
  color: #f2f2f2;
}
body {
  font-family: 'San Francisco';
}
.cat-container {
  width: 100%;
  height: 60vh;
  background-color: #dd2e44;
  top: 35vh;
  position: absolute;
}
.deal {
  width: 33vh;
  height: 50vh;
  margin: 5vh;
  background: #f2f2f2;
  display: inline-block;
  border-radius: 5px;
}
.deals-container {
  display: inline-block;
  width: 1000000px;
  overflow-x: auto;
  height: 400px;
}
.signup-form {
  width: 400px;
  margin: auto;
  font-family: 'San Francisco';
  font-weight: 100px;
  font-size: 30px;
}
.signup-inputs {
  border: 0;
  border-radius: 30px;
  color: #58595b;
  width: 400px;
  height: 60px;
  margin-bottom: 10px;
  text-align: center;
}
.signup-inputs:focus {
  outline: none;
}
.signup-button {
  border: 0;
  border-radius: 30px;
  background: #f2f2f2;
  color: #dd2e44;
  width: 400px;
  height: 60px;
  text-align: center;
  font-family: 'San Francisco';
  font-weight: 100px;
  font-size: 30px;
}
.signup-button:focus {
  outline: none;
}
.password-match-error {
  font-family: 'San Francisco';
  font-weight: 200px;
  font-size: 20px;
  color: #f2f2f2;
  text-align: center;
  margin-bottom: 0;
}
.signup-header {
  font-family: 'San Francisco';
  font-weight: 400;
  font-size: 60px;
  text-align: center;
  color: #f2f2f2;
}
.signup-footer {
  font-family: 'San Francisco';
  font-weight: 200;
  font-size: 20px;
  text-align: center;
  color: #f2f2f2;
  margin-top: 0;
}
